.tracking-container {
  border: 1px solid #ced4da;
  border-radius: 7px;
  padding: 0 1.5rem;
  margin-top: 1.5rem;
  max-height: 22rem;
  overflow-y: auto;
}

.tracking-content li:not(:last-child) {
  border-bottom: 1px solid #ced4da;
}
.shipment-history-list {
  list-style: none;
  padding: 1rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-content: flex-start;
  row-gap: 0.5rem;
}
.flex-end {
  justify-self: flex-end;
}
